import { Component, OnInit } from '@angular/core';
import { Prodotto } from '../costruttori/Prodotto';
import { ProdottiService } from '../service/prodotti.service';
import { UtilService } from '../service/util.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  assetsPathImg = '/assets/prodotti/';

  nCategoria1 = 0;
  nCategoria2 = 0;
  nCategoria3 = 0;
  nListaProd = 0;
  loaded = false;

  listaCt1: Prodotto[] = []; // nduja
  listaCt2: Prodotto[] = []; // melanzane
  listaCt3: Prodotto[] = []; // olive
  listaCompleta: Prodotto[] = [];


  constructor(private prodService: ProdottiService, private utilService: UtilService ) { }

  counter = Array;

  ngOnInit(): void {
    this.prodService.getListaProdotti()
    .subscribe (
      res => {
        this.prodService.listaProdotti = res;
        this.listaCompleta = this.prodService.listaProdotti;
        this.nListaProd = Math.ceil(this.prodService.listaProdotti.length / 3);
        // ceil arrotonda per eccesso
        this.caricaCategorie();
        this.loaded = true;
      }
    );
  }

  private caricaCategorie() {
    for (const ca of this.prodService.listaProdotti){
      if (ca.tipologia === 1) {
        this.listaCt1.push(ca);
        this.nCategoria1++;
      }
      else if (ca.tipologia === 2){
        this.listaCt2.push(ca);
        this.nCategoria2++;
      }
      else { // tipologia3
        this.listaCt3.push(ca);
        this.nCategoria3++;
      }
    }
    this.nCategoria1 =  Math.ceil(this.nCategoria1 / 3);
    this.nCategoria2 =  Math.ceil(this.nCategoria2 / 3);
    this.nCategoria3 =  Math.ceil(this.nCategoria3 / 3);

  }

  public callDettaglio(p: Prodotto) {
    this.prodService.prodotoTmp = p;
    this.utilService.redirectToComponent('dettaglio_prodotto');
    console.log(p.nome);
  }

}
